import React from 'react';
import {publicLinks} from '../../../App';
class BlogLanding extends React.Component {
    render() {
        return(
            <div className="blog wf-section">
                <div className="dv-wrapper">
                    <div className="w-layout-grid blog-grid-dv">
                        <div id="w-node-_6d8671ee-db82-cd4f-126d-a0ebd08881b5-39a5ea85" className="img-block">
                            <img
                                src="https://assets.website-files.com/620a985e7541fc8c1ca5ea83/620e00861a16117a48e478c7_img-blog.jpeg"
                                loading="lazy"
                                id="w-node-_268dd89a-8253-6a28-6c3b-f44f0bd2d79f-39a5ea85"
                                sizes="(max-width: 479px) 93vw, (max-width: 767px) 94vw, (max-width: 991px) 95vw, 96vw"
                                alt=""
                                className="image-2"
                            />
                        </div>
                        <div id="w-node-f1f6afe2-bf16-97b8-ae4f-3e28dc5af304-39a5ea85" className="beside-text">
                            <div className="beside-cont">
                                <h2 className="blog-heading">Blog</h2>
                                <div id="w-node-_5e7836f3-f5af-d564-3641-0b6cdf6fe349-39a5ea85" className="text-block">
                                    Evin ən özəl hissəsi hesab olunan yataq otağındakı mebellər, bizi rahatlıq və komfort ilə təmin etməlidir. Aldığınız mebel həm büdcəyə, həm də istəklərə uyğun olmalıdır. Yataq otağı mebeli seçərkən nəzərə almalı
                                    olduğunuz əsas nüansları sizin üçün araşdırdıq.
                                </div>
                                <a href={publicLinks.login} className="btn-blog-area-2 w-inline-block">
                                    <div className="btn-slide-2">DAVAMI</div>
                                    <div className="btn-growing-div" />
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default BlogLanding;