
import deleteBTN from '../../../assets/images/delete.svg';
import sampleproductimage from '../../../assets/images/61a0183a6b729668a9f894a9_square-02-p-500.jpeg';
import closeIcon from '../../../assets/images/icon-cross.svg';

export default function Wishlist() {
    function closeWishModal() {
        document.getElementById("Wishlist").style.display = "none";
      }
    return(
        <div className="wishlist" id="Wishlist">
            <div className="cart-wrapper">
                <div className="cart-container">
                    <div className="cart-header">
                        <h4>Favoritlər</h4>
                        <a className="close-cart wh-24 w-inline-block" onClick={closeWishModal}>
                            <img src={closeIcon} loading="lazy" alt="" />
                        </a>
                    </div>
                    <div className="cart-form">
                        <div className="active-state">
                            <div className="w-layout-grid prd-grids">

                                <div id="w-node-f10a1b06-03d3-c0d3-94aa-a20fe9af670e-39a5ea85" className="crt-items">
                                    <div className="product-img-block">
                                        <img src={sampleproductimage} loading="lazy" alt="" className="product-image" />
                                    </div>
                                    <div className="crt-item-details">
                                        <div className="crt-product-det">
                                            <div className="cart-prd-name">Palma qonaq dəsti</div>
                                            <div className="cart-prd-price">1000 AZN</div>
                                        </div>
                                        <div className="cart-prd-remove">
                                            <a id="w-node-f10a1b06-03d3-c0d3-94aa-a20fe9af6717-39a5ea85" href="#" className="remove-block w-inline-block">
                                                <img src={deleteBTN} loading="lazy" id="w-node-e5717e3a-c2a9-3441-feb3-8a6409123d88-39a5ea85" alt="" className="wh-20" />
                                            </a>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    <div id="w-node-f10a1b06-03d3-c0d3-94aa-a20fe9af6741-39a5ea85" className="empty-state">
                        <div className="empty-text-block">No items found.</div>
                    </div>
                    <div id="w-node-f10a1b06-03d3-c0d3-94aa-a20fe9af6744-39a5ea85" className="error-state">
                        <div>Something went wrong when adding this item to the cart.</div>
                    </div>
                </div>
                </div>
            </div>
            </div>

    )
}