import React, {useState, useEffect} from 'react'
import agent from "../../api/agent"
 

function UnvanForm({element, unvanFormChangeFunc, products, unvanForm, unvanIncrease, customerAdress}) {
    const [cities, setCities]=useState([])
    const [regions, setRegions]=useState([])
 
  
    useEffect(async () => {
        const cities = await agent.BucketRelated.getCities()
        const regions = await agent.BucketRelated.getCities(cities[0].id)
         setCities(cities)
        setRegions(regions)
        }, [])


        const onCityChange= async (e)=>{
            const regions = await agent.BucketRelated.getCities(e.target.value)
             setRegions(regions)
            unvanFormChangeFunc(e)
         }


 
 const resetCustomerAddress=()=>{
  unvanFormChangeFunc({
    target:{
      value:null,
      name:"addressId;"+element
    }
  })
  unvanForm[element].addressId=null
    }



   useEffect(() => {
 
   }, [])
   

     return (
    <div key={element} className="shipping">
     <div className="block-header">
      <h4 className="checkout-heading">Çatdırılma ünvanı</h4>
      <div className="required">* Tələb olunur </div>
    </div>
 
  
    <fieldset className="w-commerce-commercecheckoutblockcontent block-content">
      <div className="">
      <select onChange={unvanFormChangeFunc} className="selectCustom selectCustom_mr_5" name={"productId;"+element} id=""> { products.map((product)=> <option key={product.id} value={product.id}>{product.title}</option>) } </select>
      <select onChange={unvanFormChangeFunc} className="selectCustom" name={"addressId;"+element} id=""> { customerAdress.map((customerAddress)=> <option key={customerAddress.id} value={customerAddress.id}>{customerAddress.address}</option>) } </select>

{unvanForm[element].addressId && <button onClick={resetCustomerAddress}>Adres elave et</button>  }
       </div>
{!unvanForm[element].addressId &&
       <> 
      <div className="w-commerce-commercecheckoutrow block-row">
        <div className="w-commerce-commercecheckoutcolumn block-column">
          <label className="w-commerce-commercecheckoutlabel cs-label">Şəhər *</label>
          <select onChange={onCityChange} name={"seher;"+element} className="w-commerce-commercecheckoutshippingcountryselector city-lb">
               {cities.map((city)=> <option key={city.id} value={city.id}>{city.name}</option>)}
         </select>
        </div>

     <div className="w-commerce-commercecheckoutcolumn block-column gap-b-16">
     <label className="w-commerce-commercecheckoutlabel cs-label">Çatdırılma tarixi *</label>
          <input value={unvanForm[element]['tarix']} onChange={unvanFormChangeFunc} type="datetime-local" name={"tarix;"+element} required className="w-commerce-commercecheckoutshippingcity dist-lb" />
          </div>

      </div>
      <div className="w-commerce-commercecheckoutrow block-row">
        <div className="w-commerce-commercecheckoutcolumn block-column">
          <label className="w-commerce-commercecheckoutlabel cs-label">Çatdırılma ünvanı *</label>
          <input value={unvanForm[element]['unvan']} onChange={unvanFormChangeFunc} type="text" name={"unvan;"+element} required className="w-commerce-commercecheckoutshippingcity loc-lb" />
        </div>
        <div className="w-commerce-commercecheckoutcolumn block-column gap-b-16">
         
          { regions.length>0 && <>
          <label className="w-commerce-commercecheckoutlabel cs-label">Rayon *</label>
          <select onChange={onCityChange} name={"seher;" + element} className="w-commerce-commercecheckoutshippingcountryselector city-lb">
               {regions.map((city)=> <option key={city.id} value={city.id}>{city.name}</option>) }
         </select>
         </>
         }
        </div>
      </div>
      <div className="w-commerce-commercecheckoutrow block-row">
        <div className="w-commerce-commercecheckoutcolumn block-column">
          <label className="w-commerce-commercecheckoutlabel cs-label import">Əlavə qeydlər</label>
          <input value={unvanForm[element]['elaveMelumat']} onChange={unvanFormChangeFunc} type="text" name={"elaveMelumat;"+element} required className="w-commerce-commercecheckoutshippingcity notes-lb" />
        </div>
      </div>
      </>}
      <button onClick={unvanIncrease} className="add-location w-inline-block w-100">
        <div className="add-loc-name-2">Digər məhsulu fərqli ünvana çatdıraq</div>
      </button>
    </fieldset>
  </div>
  )
}

export default UnvanForm ;