import React from 'react';
import {publicLinks} from '../../../App';
import heroSliderImg from '../../../assets/images/hero-slider.jpeg';
import { Link } from "react-router-dom";
export default function HeroSlider() {
    return(
        <div className="hero-slider wf-section">
            <div className="w-layout-grid hero-grid">
                <div id="w-node-bf980d3e-df69-3278-3bda-2a5fa6247eef-39a5ea85" className="hero-right bg-color">
                    <div className="content-row">
                        <h1 className="slider-main-text">Güzəştli şərtlərlə indi al hissə-hissə ödə!</h1>
                        <div className="slider-desc">Enjoy the savings, now through 2/21. Exclusions apply.</div>
                        <Link to={publicLinks.product} className="btn-slide-area w-inline-block">
                            <div className="btn-slide">Hİssə-hİssə al</div>
                        </Link>
                    </div>
                </div>
                <img
                    src={heroSliderImg}
                    loading="lazy"
                    sizes="100vw"
                    id="w-node-abeb76eb-7d1a-400d-5d62-f2c117a903e3-39a5ea85"alt=""
                    className="hero-image"
                />
            </div>
        </div>
    )
}