import React, { Component } from 'react';
import "react-image-gallery/styles/css/image-gallery.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

 

export default class ItemGallery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nav1: null,
      nav2: null
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2
    });
  }
  render() {
     const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      arrows: false,
      slidesToShow: 1,
      slidesToScroll: 1,
      swipeToSlide: true,
      asNavFor: this.state.nav2,
      className: "slider-cs",
      ref: slider => (this.slider1 = slider),
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            className: "slider-cs-tab",
          }
        },
      ]
    };
    return (
      <div>
         <Slider {...settings}>
          {
            this.props.images.map((image)=>{
              return <div>
              <img src={'https://cdn.otomall.az/'+image} alt="" />
            </div>
            })
          }
        
         
     
        </Slider>
        <Slider
          asNavFor={this.state.nav1}
          ref={slider => (this.slider2 = slider)}
          slidesToShow={4}
          swipeToSlide={true}
          focusOnSelect={true}
          className="thumb-slider-cs"
        >
 
          {
            this.props.images.map((image)=>{
              return  <img className='thumb-image-cs' src={'https://cdn.otomall.az/'+image} alt="" />
             
             })
          }
        </Slider>
      </div>
    );
  }
}